.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;

}
.modal.active {
  opacity: 1;
  pointer-events: all;
}
.modal__content {
  padding: 20px;
  border-radius: 20px;
  background-color: #fff;
  transform: scale(0.5);
  width: 762px;
  display: flex;
    flex-wrap: wrap;
}
.modal__content.active {
  transform: scale(1);
}
.pretitle {
  font-weight: 500;
  font-size: 32px;
  line-height: 140.5%;
  width: 300px;
  margin-right: 20px;
  margin-top: 30px;
  margin-bottom: 0px;
}
.contained{
  margin-right: 5px;
}
.container__button{
  display: flex;
  gap: 15px;
}
